code,
pre {
  border: none; }
  code[class*='language-'],
  pre[class*='language-'] {
    background: none;
    color: #393a34;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: .75rem;
    hyphens: none;
    line-height: 1.5;
    tab-size: 4;
    text-align: left;
    text-shadow: 0 1px #fff;
    white-space: pre;
    word-break: normal;
    word-spacing: normal;
    word-wrap: normal; }
    code[class*='language-']::selection,
    code[class*='language-'] ::selection,
    pre[class*='language-']::selection,
    pre[class*='language-'] ::selection {
      background: #b3d4fc;
      text-shadow: none; }
    @media print {
      code[class*='language-'],
      pre[class*='language-'] {
        text-shadow: none; } }

pre[class*='language-'] {
  margin: .5rem 0;
  overflow: auto;
  padding: 1rem; }
  pre[class*='language-'].line-numbers {
    padding-left: 3.8rem; }

:not(pre) > code[class*='language-'],
pre[class*='language-'] {
  background: #f5f2f0; }

:not(pre) > code[class*='language-'] {
  border-radius: .3rem;
  padding: .1rem;
  white-space: normal; }

.token.comment, .token.prolog, .token.doctype, .token.cdata {
  color: #008000;
  font-style: italic; }

.token.namespace {
  opacity: .7; }

.token.string {
  color: #a31515; }

.token.punctuation, .token.operator {
  color: #393a34; }

.token.url, .token.symbol, .token.number, .token.boolean, .token.variable, .token.constant, .token.inserted {
  color: #36acaa; }

.token.atrule, .token.keyword, .token.attr-value {
  color: #00f; }

.language-autohotkey .token.selector, .language-css {
  color: #00f; }

.token.function {
  color: #393a34; }

.token.deleted {
  color: #9a050f; }

.language-autohotkey .token.tag {
  color: #9a050f; }

.token.selector {
  color: #00009f; }

.language-autohotkey .token.keyword {
  color: #00009f; }

.token.important, .token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.class-name {
  color: #2b91af; }

.token.tag, .token.selector {
  color: #800000; }

.token.attr-name, .token.property, .token.regex, .token.entity {
  color: #f00; }

.token.directive.tag .tag {
  background: #ff0;
  color: #393a34; }

.line-numbers {
  counter-reset: linenumber;
  padding-left: 3.8rem;
  position: relative; }
  .line-numbers > code {
    position: relative; }
  .line-numbers .line-numbers-rows {
    border-right: 1px solid #808080;
    font-size: 100%;
    left: -3.8rem;
    letter-spacing: -1px;
    pointer-events: none;
    position: absolute;
    top: 0;
    user-select: none;
    width: 3rem; }
    .line-numbers .line-numbers-rows > span {
      counter-increment: linenumber;
      display: block;
      pointer-events: none; }
      .line-numbers .line-numbers-rows > span::before {
        color: #808080;
        content: counter(linenumber);
        display: block;
        padding-right: .5rem;
        text-align: right; }

/*
    MENU COMPONENT
*/
.menu {
  font-size: .9rem; }
  .menu a {
    color: #808080; }
    .menu a:hover {
      color: #fff; }
    .menu a.button {
      color: #fff; }
  .menu.dropdown > li.is-dropdown-submenu-parent > a.dropdown-no-arrow {
    padding-right: 1rem; }
    .menu.dropdown > li.is-dropdown-submenu-parent > a.dropdown-no-arrow::after {
      content: none; }
  .menu.dropdown .is-dropdown-submenu {
    z-index: 9999; }
  .menu .button-group {
    margin-bottom: 0; }

/*
    VIEWER COMPONENT
*/
.viewer .grid-frame {
  background-color: #808080;
  height: calc(100vh - 32px);
  position: inherit; }

.viewer-cell {
  background-color: #fff;
  transition: max-width linear .5s; }
  .viewer-cell--full {
    max-width: 100%; }
  .viewer-cell--large {
    max-width: 70.25rem; }
  .viewer-cell--medium {
    max-width: 33.75rem; }
  .viewer-cell--small {
    max-width: 28.75rem; }

.viewer-frame {
  width: 100%;
  height: 100%;
  border: none; }
